@import url('https://fonts.googleapis.com/css2?family=Sofia+Sans+Extra+Condensed:wght@200;300&display=swap');

body,
html {
  margin: 0;
  padding: 0;
  font-family: 'Sofia Sans Extra Condensed', sans-serif;
}
.container {
  max-width: 100%;
  max-height: 100%;
  display: grid;
  justify-items: center;
  align-content: center;
}

/* HEADER */
.sticky {
  top: 0;
  left: 0;
  width: 100%;
  position: sticky;
  background-color: white;
  padding: 1rem 0 1rem 0;
  display: flex;
  justify-content: space-around;
  align-content: center;
  box-shadow: 4px 4px 10px 0px rgba( 0, 0, 0, .5 );
  z-index: 3;
}
.sticky > p {
  width: 25rem;
  height: 3rem;
  margin: 0;
  text-align: left;
  font-weight: 300;
  font-size: 3rem;
  
}

/* SEARCHBOX */
/* .searchContainer {
  
} */
.searchInput {
  text-align: center;
  width: 25rem;
  height: 3.5rem;
  font-family: 'Sofia Sans Extra Condensed', sans-serif;
  font-size: 2rem;
}

/* CARDS */
.cardContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}

/* FOOTER */
.footer {
  height: 1rem;
  width: 10000px;
  bottom: 0;
  position: fixed;
  background-color: white;
  box-shadow: 4px 4px 10px 0px rgba( 0, 0, 0, .5 );
  text-align: center;
  z-index: 3;
}
.footer > p {
  margin: 0;
  padding: 0;
}